<template>
    <div class="remind">
        <navigate title="航行提醒" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-list class="pn">
                <mu-list-item class="border-1px-b" avatar>
                    <mu-list-item-content>
                        <mu-list-item-title>语音播报提醒</mu-list-item-title>
                        <mu-list-item-sub-title>在航行中实时向您播报安全信息</mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>
                        <mu-switch :input-value="speech" @change="handlerSpeech"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item class="border-1px-b" avatar>
                    <mu-list-item-content>
                        <mu-list-item-title>文字提醒</mu-list-item-title>
                        <mu-list-item-sub-title>将助航信息以文字形式显示在地图上方</mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>
                        <mu-switch :input-value="text" @change="handlerText"/>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item avatar>
                    <mu-list-item-content>
                        <mu-list-item-title>语音性别</mu-list-item-title>
                        <mu-list-item-sub-title>切换航行提醒的声音主题</mu-list-item-sub-title>
                    </mu-list-item-content>
                    <mu-list-item-action>
                        <mu-checkbox :input-value="speechGender === SPEECH_GENDER_WOMAN.value"
                                     :value="SPEECH_GENDER_WOMAN.value"
                                     color="secondary"
                                     uncheck-icon="sentiment_satisfied_alt"
                                     checked-icon="face"
                                     @change="toggleSpeechGender"/>
                    </mu-list-item-action>
                </mu-list-item>
            </mu-list>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {appMixins} from "../../../common/mixins"

    export default {
        name: "remind",
        mixins: [appMixins],
        data() {
            return {
                events: true,
                phone: false,
                gender: false
            }
        },
        methods: {
            handlerSpeech(value) {
                this.setSpeech(value)
            },
            handlerText(value) {
                this.setText(value)
            },
            toggleSpeechGender(value) {
                value = value ? this.SPEECH_GENDER_WOMAN.value : this.SPEECH_GENDER_MAN.value

                this.setSpeechGender(value)
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .remind
        padding-top 46px
        background-color #EFEFEF
</style>



